body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: white;
  color: #333;
}

h1 {
  text-align: center;
  margin-top: 20px;
}

.month-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 20px 0;
}

.month-navigation button {
  padding: 10px 15px;
  font-size: 16px;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.month-navigation button:hover {
  background-color: #0056b3;
}

.month-navigation div {
  font-size: 18px;
  font-weight: bold;
}